.demo-img{
    width:0;
    height: auto;
    border-radius: 8px;
    opacity: 0;
    transition: opacity 0.5s ease-in;
    
}

.demo-img.active {
    opacity: 1; /* Show the active image */
    width: 100% ;
}


.dot-btn{
    border-radius:70px;
    padding: 0 5px;
    border: none;
    color:#98ADB1;
    background-color: #98ADB1;
    cursor: pointer;
    margin: 5px 8px;
    font-size: 11px;
}


.dot-btn.active{
    background-color: hsl(184, 100%, 57%);
}

#img-carousel{
    display:flex;
    flex-direction: column;
    align-items: center;
}

#carousel-btns{
    display:flex;
    flex-direction: row;
    justify-items: space-evenly;
    color: #0a9bb8;
}

.showcase{
    position : relative;
}

.project-info{
    height: fit-content;
    margin-left: 2%;
}

#triangle-btn-right{
    width: 0; 
    height: 0; 
    border-top: 15px solid transparent;
    border-bottom: 15px solid transparent;
    border-left: 15px solid #98ADB1;
    margin-left: 2%;
    position:relative;
}

#triangle-btn-left{
    width: 0; 
    height: 0; 
    border-top: 15px solid transparent;
    border-bottom: 15px solid transparent;
    border-right: 15px solid #98ADB1;
    margin-right: 2%;
}


#triangle-btn-right:hover{
    border-left:solid 15px hsl(184, 100%, 57%);
}

#triangle-btn-left:hover{
    border-right:solid 15px hsl(184, 100%, 57%);
}

.skill-bubble{
    /* background-color: #0c373d ; */
    background-color: hsl(213, 45%, 25%);
    border-radius: 10px;
    width: fit-content;
    padding: 4px 9px;
    margin-left:35px;
    color:#98ADB1;
    margin-bottom: 2%;
}

#carousel{
    display: flex;
    flex-direction: row;
    align-items: center;
}

#skills-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

.icon{
    color: #E9F4F6;
}

.icon:hover{
    color:hsl(184, 100%, 57%);
    cursor: pointer;
}

#all-projects-btn{
    padding: 4px 10px;
    /* color: #6DF1B3; */
    color: hsl(184, 100%, 57%);
    border-radius : 6px;
    /* border: 2px solid #6DF1B3; */
    border: 2px solid hsl(184, 100%, 57%);

}

span{
    /* color:#6DF1B3; */
    color : hsl(184, 100%, 57%);
}